import { Component, Input, OnInit } from '@angular/core';
import { ChartOptions, ChartType } from 'chart.js';
import * as pluginAnnotation from 'chartjs-plugin-annotation';
import { Label } from 'ng2-charts';
import {
  option,
  History,
  Question,
  Questions,
} from 'src/app/models/surveyResponse';

@Component({
  selector: 'app-response-analytics',
  templateUrl: './response-analytics.component.html',
  styleUrls: ['./response-analytics.component.scss'],
})
export class ResponseAnalyticsComponent implements OnInit {
  @Input() chartItem!: Questions;
  isDashboardTypeTrend: boolean = false;
  isDashboardTypeIcons: boolean = false;
  isDashboardTypeText: boolean = false;
  isDashboardTypeEmpty: boolean = false;
  matchedWithBound: boolean = false;
  questionLabel!: string;
  questionText!: string;
  selectedBoundValue: string = '';
  completeAnswer?: string;
  dbtEmptyAnswer?: string;
  selectedOptionofTypeIcons: string = '';
  defaultBound?: string;
  questionOptions: option[] = [];
  lineChartOptions!: ChartOptions;
  iconTypeHistories: History[] = [];
  dashboardTrendYAxisValue: any;
  maxValue: any;
  dashboardTrendArr: any[] = [];
  // questionList: Question[];
  filteredArr: boolean = false;

  /**
   * sets chart options
   */
  setupChartOptions() {
    this.lineChartOptions = {
      responsive: true,
      layout: {
        padding: 10,
      },
      scales: {
        yAxes: [
          {
            ticks: {
              suggestedMax: this.dashboardTrendYAxisValue,
              stepSize: 0,
              beginAtZero: true,
            },
          },
        ],
        xAxes: [
          {
            display: true,
            ticks: {
              suggestedMin: 0,

              display: false,
            },
          },
        ],
      },
      annotation: {
        drawTime: 'afterDatasetsDraw',
        annotations: [
          {
            type: 'box',
            xMin: 1,
            xMax: 0,
            yMin: 0,
            yMax: 100,
            backgroundColor: 'rgba(255, 255, 255, 0.5)',
            borderWidth: 0,
          },
          {
            type: 'line',
            mode: 'horizontal',
            scaleID: 'y-axis-0',
            value: this.setThresholdPosition(),
            borderColor: this.gatSelectedBoundValueColor(),
            borderWidth: 2,
            borderDash: [5, 3],
          },
          {
            type: 'line',
            mode: 'horizontal',
            scaleID: 'y-axis-0',
            value: this.setThresholdPosition(),
            borderColor: 'transparent',
            label: {
              backgroundColor: 'transparent',
              xPadding: 5,
              yPadding: 5,
              fontSize: 30,
              content: this.gatSelectedBoundValue(),
              fontColor: this.gatSelectedBoundValueColor(),
              enabled: true,
            },
          },
        ],
      },
    };
  }

  public lineChartLabels: Label[] = [];
  public lineChartType: ChartType = 'line';
  public lineChartLegend = false;
  public lineChartPlugins = [pluginAnnotation];

  public lineChartData = [
    {
      data: [],
      fill: true,
      borderColor: 'rgb(75, 192, 192)',
      backgroundColor: 'rgba(75, 192, 192, 0.2)',
      tension: 0.2,
    },
  ];

  /**
   * Constructor
   */
  constructor() {}

  /**
   * on component load
   */
  ngOnInit(): void {
    this.questionLabel = this.chartItem?.question?.questionLabel;
    this.questionText = this.chartItem?.question?.questionText;

      if (this.chartItem.question.dashboardType == 'Trend') {
        this.defaultBound =
          this.chartItem?.bound == undefined
            ? this.chartItem?.question?.bound
            : this.chartItem?.bound;
        this.isDashboardTypeTrend = true;
        this.setupChartForDashboardTypeTrend();
        this.setDashboardTrendYAxisValues();
        this.setupChartOptions();
      } else {
        this.isDashboardTypeTrend = false;
      }
      if (this.chartItem.question.dashboardType == 'Icons') {
        this.defaultBound =
          this.chartItem?.bound == undefined
            ? this.chartItem?.question?.bound
            : this.chartItem?.bound;
        this.isDashboardTypeIcons = true;
        this.setupChartForDashboardTypeIcons();
        this.setupChartOptions();
      } else {
        this.isDashboardTypeIcons = false;
      }

      if (this.chartItem.question.dashboardType == 'Text') {
        this.defaultBound =
          this.chartItem?.bound == undefined
            ? this.chartItem?.question?.bound
            : this.chartItem?.bound;
        this.isDashboardTypeText =
          this.chartItem.question.questionLabel == 'Message for Clinician'
            ? this.chartItem?.question?.history.length
              ? true
              : false
            : true;
        this.completeAnswer =
          this.chartItem.question.questionLabel == 'Message for Clinician'
            ? this.chartItem?.question?.history[0]?.response
            : this.chartItem?.question?.value;
      } else {
        this.isDashboardTypeText = false;
      }

      if (this.chartItem.question.dashboardType == '' || '') {
        this.defaultBound =
          this.chartItem?.bound == undefined
            ? this.chartItem?.question?.bound
            : this.chartItem?.bound;

        this.isDashboardTypeEmpty = true;
        this.dbtEmptyAnswer = this.gatSelectedBoundValue();
      } else {
        this.isDashboardTypeEmpty = false;
      }
  }

  /**
   * set-up tend type chart options dynamically.
   */
  setDashboardTrendYAxisValues() {
    this.dashboardTrendArr = [];
    this.defaultBound =
      this.chartItem?.bound == undefined
        ? this.chartItem?.question?.bound
        : this.chartItem?.bound;
        if(this.chartItem?.question?.history != null) {
          const newHist = this.chartItem?.question?.history;
          newHist?.forEach((o: any) => {
            this.dashboardTrendArr.push(o?.response);
            this.dashboardTrendArr.sort();
          });
        }
        else{
          const newHist = this.chartItem?.question?.options;
          newHist?.forEach((o: any) => {
            o.selected == true ? this.dashboardTrendArr.push(o.label) : ''
          });
        }
  
      this.maxValue = this.dashboardTrendArr[this.dashboardTrendArr.length - 1];
      if (Number(this.defaultBound) > parseInt(this.maxValue)) {
        if (Number(this.defaultBound) < 10) {
          this.dashboardTrendYAxisValue = 10;
        } else {
          this.dashboardTrendYAxisValue = Number(this.defaultBound) + 5;
        }
      } else {
        if (parseInt(this.maxValue) < 10) {
          this.dashboardTrendYAxisValue = 10;
        } else {
          this.dashboardTrendYAxisValue = parseInt(this.maxValue) + 5;
        }
      }
  }

  /**
   * setsup chart for dashboard type trend
   */
  setupChartForDashboardTypeTrend() {
    if (this.chartItem.question.history != null) {
      const history = this.chartItem.question.history.slice().reverse();
      this.lineChartLabels = history.reduce(
        (a: any, o: any) => (o.responseDate && a.push(o.responseDate), a),
        []
      );
      this.lineChartData[0].data = history.reduce(
        (a: any, o: any) => (o.response && a.push(o.response), a),
        []
      );
    }
  }

  /**
   * setting up chart fot dashboard type icons
   */
  setupChartForDashboardTypeIcons() {
    if (this.chartItem.question.history != null) {
      this.iconTypeHistories = this.chartItem.question.history
        .slice()
        .reverse();
    }
    this.selectedOptionofTypeIcons = this.gatSelectedBoundValue();
    if (this.defaultBound?.includes(this.selectedOptionofTypeIcons)) {
      this.matchedWithBound = true;
    }
  }

  /**
   * sets bound value as threshold
   * @returns
   */
  setThresholdPosition() {
    return Number(this.defaultBound);
  }

  /**
   * gets selected bound value
   * @returns
   */
  gatSelectedBoundValue() {
    if (this.chartItem?.question?.options != null) {
      this.questionOptions = this.chartItem?.question?.options;
      this.filteredArr = this.questionOptions.some(
        (o: any) => o.alarming && o.selected
      );
      for (let i = 0; i < this.questionOptions.length; i++) {
        if (this.questionOptions[i].selected) {
          this.selectedBoundValue = this.questionOptions[i].label;
        }
      }
    } else {
      this.selectedBoundValue = String(
        this.chartItem?.question?.history[0].response
      );
    }
    return this.selectedBoundValue;
  }

  /**
   * gets selected bound value color
   * @returns
   */
  gatSelectedBoundValueColor() {
    const bound = Number(this.defaultBound);
    const selectedBound = Number(this.selectedBoundValue);
    if (selectedBound >= bound) {
      return '#dc3644'; // Red Color
    } else {
      return '#666'; // Gray Color
    }
  }

  /**
   * gets tool tip value
   * @param history
   * @returns
   */
  gettoottipValue(history: any) {
    let label =
      history.questionLabel == undefined
        ? this.chartItem.question.questionLabel
        : history.questionLabel;
    return `${label} \n ${history.responseDate} : ${history.response}`;
  }
}
