import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import {
  Disorders,
  PatientDetails,
  PatientHistory,
  Questions,
  SurveyResponse,
  SurveyReviewResponse,
  chartDetails
} from 'src/app/models/surveyResponse'
import { LoaderService } from '../../services/loader.service'
import * as pluginAnnotation from 'chartjs-plugin-annotation'
import { Label } from 'ng2-charts'
import { ChartOptions, ChartType } from 'chart.js'
import { PatientOverviewService } from '../../services/patient-overview.service'
import { Subscription } from 'rxjs'
import { Location } from '@angular/common'
import {
  PatientHistoryResponseData,
  PatientOverviewHeadingData,
  PatientResponseSummaryData,
} from 'src/app/models/patientOverview'
import { FormBuilder } from '@angular/forms';
import { ResponseAnalyticsComponent } from './response-analytics/response-analytics.component'

@Component({
  selector: 'app-patient-overview',
  templateUrl: './patient-overview.component.html',
  styleUrls: ['./patient-overview.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PatientOverviewComponent implements OnInit {
  viewOptions: any
  routeParam: Subscription = new Subscription()

  @ViewChild(ResponseAnalyticsComponent) chartItem!: Questions
  chartsdata: any[] = []
  msgsForClinician: Questions[] = []
  surveyDataReview!: SurveyReviewResponse
  cycleId!: number
  painStatus!: string
  surveyResponseScore!: number
  surveyId?: number = 259
  surveyType!: string
  surveyResponseId!: number
  surveyResponse!: SurveyResponse
  patientDetails!: PatientDetails
  patientDisorders: Disorders[] = []
  patientComorbidities: Disorders[] = []
  responseQuestions!: Questions[]
  painDetails!: chartDetails
  cravingsDetails!: chartDetails
  painScoreOptions!: ChartOptions
  cravingsScoreOptions!: ChartOptions
  charts: chartDetails[] = []
  error: string = ''
  showErrorDialog: boolean = false
  patientMessage?: string
  questionHistory?: Questions
  questionHistoryFlag: boolean = false
  patientHistory!: PatientHistory
  patientId: any
  patientHeadingData!: PatientOverviewHeadingData
  monthNames: any[] | undefined
  today: any
  last3Months: any
  msgs: any
  msgsFlag: any
  month: string = '0'
  year: string = '0'
  patientResponseSummaryData?: PatientResponseSummaryData
  patientHistoryData: PatientHistoryResponseData[] = []
  patientData: any[] | undefined
  isMsgs: any[] = []
  displayManualTimeEntry: boolean = false;
  view: boolean = false;
  usersList!: any[];
  startDate: string = '';
  endDate: string = '';
  totalDays!: number;
  addReviewTimeDates: any[] =[];
  addReviewTimeStartDate: any;
  addReviewTimeDatesArr: any[] = [];
  addReviewTimeEntries: any[] = [];
  addReviewTimeEntry: any[] = [];
  /**
   * Pain Score response chart properties
   */
  public painScoreLabels: Label[] = []
  public painScoreType: ChartType = 'line'
  public painScoreLegend = false
  public painScorePlugins = [pluginAnnotation]

  public painScoreData = [
    {
      data: [],
    },
  ]

  /**
   * Craving Score response chart properties
   */
  public cravingsScoreLabels: Label[] = []
  public cravingsScoreType: ChartType = 'line'
  public cravingsScoreLegend = false
  public cravingsScorePlugins = [pluginAnnotation]

  public cravingsScoreData = [
    {
      data: [],
    },
  ]

  
  constructor(
    public fb: FormBuilder,
    public loaderService: LoaderService,
    public patientOverviewService: PatientOverviewService,
    private route: ActivatedRoute,
    private location: Location,
  ) {
    this.loaderService.show();
    this.routeParam = this.route.params.subscribe(
      (params: any) => {
        this.patientId = params['patientId']
      },
      (error: any) => {
        console.log(error)
        this.location.back()
      },
    )
  }


  /**
   *  Gets the patient summary
   */
  getPatientResponse() {
    this.chartsdata = []
    this.isMsgs = []
    this.loaderService.show()

    this.patientOverviewService.combinedResponse(this.patientId).subscribe(
      (res: any) => {
        if(res!=null){
          this.loaderService.hide();
          this.patientHeadingData = res.headerData;
                this.patientResponseSummaryData = res.summaryData;
                if (this.patientResponseSummaryData?.questions != null) {
                  const allQuestions = this.patientResponseSummaryData?.questions
                  for (let i = 0; i < allQuestions.length; i++) {
                     this.chartsdata.push({ question: allQuestions[i] });
                  }
                }
                this.chartsdata = this.chartsdata.filter(
                  (o: any) =>
                    o.question.questionLabel != 'Message for Clinician' ||
                    o.question.history.length != 0,
                )
                if (this.surveyDataReview?.response?.questions != null) {
                  this.responseQuestions = this.surveyDataReview?.response?.questions
    
                  const painScoreQuestionObj = this.responseQuestions.filter(
                    (o: any) => o.id === 1,
                  )
                }
    
                this.painDetails = {
                  Options: this.painScoreOptions,
                  Labels: this.painScoreLabels,
                  Type: 'line',
                  Legend: false,
                  Annotation: [pluginAnnotation],
                  Data: [
                    {
                      data: this.painScoreData[0].data,
                    },
                  ],
                }
    
                if (this.painDetails != null) {
                  this.charts.push(this.painDetails)
                }
              }
      },
      (err: any) => {
        console.log(err);
         this.loaderService.hide();
      }
    );

}


  /**
   * On component load
   */
  ngOnInit(): void {
    window.scrollTo({ top: 0 })
    this.getPatientResponse()
  }

}
